<template>
  <div>
    <h1>Fund List</h1>
    <!-- <ul>
      <li v-for="stock in stockList" :key="stock.symbol">
        {{ stock.symbol }} - {{ stock.name }}
      </li>
    </ul> -->
  </div>
</template>

<script>
// import FundListTable from "@/components/FundListTable.vue";
import { apiService } from "@/services/fundAPI";

export default {
  name: "StockList",

  data() {
    return {
      stockList: [],
    };
  },
  async created() {
    await this.loadFunds();
  },
  methods: {
    async loadFunds() {
      try {
        this.stockList = await apiService.fetchStockList();
      } catch (error) {
        console.error("Error fetching stock list:", error);
      }
    },
  },
};
</script>
