import axios from "axios";
import { message } from "ant-design-vue";

const API_KEY = "135e0789b5c24be6a9fcf196a28ab290"; // Replace with your Twelve Data API key
const API_BASE_URL = "https://api.twelvedata.com";
const axiosInstance = axios.create({
  baseURL: API_BASE_URL,
  withCredentials: true,
  responseType: "json",
});

axiosInstance.interceptors.request.use((config) => {
  console.log(config);
  return {
    ...config,
    headers: {
      ...config.headers,
      Authorization: "apikey " + API_KEY,
      "Access-Control-Allow-Origin": "*",
    },
  };
});

class BaseApiService {
  async get(path, params) {
    path = String(path);
    path = params
      ? `${path}?${Object.entries(params)
          .map(([key, value]) => `${key}=${value}`)
          .join("&")}`
      : path;

    return axiosInstance
      .get(path)
      .then((res) => res.data)
      .catch((err) => this.errorHandler(err));
  }

  isError(code) {
    return !(
      code.toString().startsWith("2") || code.toString().startsWith("3")
    );
  }

  showMessage =
    (isSuccessDisplay = false) =>
    (res) => {
      const { code, msg } = res;
      const isError = this.isError(code);

      if (isError) {
        message.error(msg);
      }

      if (isSuccessDisplay && !isError) {
        message.success(msg);
      }

      return res;
    };

  errorHandler(err) {
    const msg = err.message;
    const code = err.code;

    return { msg, code };
  }
}

class ApiService extends BaseApiService {
  fetchStockList() {
    return this.get("/api/stocks").then(this.showMessage(false));
  }
}

